import React, { useEffect, useMemo, useState } from 'react';
import tMain from '../data/mainList';

export default function Main({ lang }) {
  const [viewLang, setViewLang] = useState(lang || 'ru');
  const [fading, setFading] = useState(false);
  useEffect(() => {
    if (!lang) return;
    if (lang === viewLang) return;
    setFading(true);
    const id = setTimeout(() => { setViewLang(lang); setFading(false); }, 160);
    return () => clearTimeout(id);
  }, [lang, viewLang]);

  const t = (tMain && (tMain[viewLang] || tMain.ru || tMain.en)) || {};

  // Лёгкое определение темы (синхронно под светлую/тёмную)
  const getInitialTheme = () => {
    if (typeof window !== 'undefined') {
      const saved = localStorage.getItem('theme');
      if (saved === 'light' || saved === 'dark') return saved;
      if (window.matchMedia && window.matchMedia('(prefers-color-scheme: dark)').matches) return 'dark';
    }
    return 'light';
  };

  const [theme, setTheme] = useState(getInitialTheme);

  useEffect(() => {
    const mq = window.matchMedia ? window.matchMedia('(prefers-color-scheme: dark)') : null;
    const onMq = (e) => {
      // Обновляем тему только если пользователь явно не выбрал её раньше
      const saved = localStorage.getItem('theme');
      if (!saved) setTheme(e.matches ? 'dark' : 'light');
    };
    const onApp = (e) => {
      const next = e.detail;
      if (next === 'light' || next === 'dark') setTheme(next);
    };
    mq && (mq.addEventListener ? mq.addEventListener('change', onMq) : mq.addListener(onMq));
    window.addEventListener('app-theme-changed', onApp);
    // Синхронно подтянуть текущее из localStorage (на случай если мы монтируемся позже)
    const saved = localStorage.getItem('theme');
    if (saved === 'light' || saved === 'dark') setTheme(saved);
    return () => {
      mq && (mq.removeEventListener ? mq.removeEventListener('change', onMq) : mq.removeListener(onMq));
      window.removeEventListener('app-theme-changed', onApp);
    };
  }, []);

  const colors = useMemo(() => {
    const common = {
      accent: '#229ED9',              // голубой-акцент (телеграм-стайл)
      accentSoft: 'rgba(34,158,217,0.15)',
      success: '#5BE49B',
      warning: '#FFD66B',
      focus: 'rgba(34,158,217,0.35)',
      radiusLg: 18,
      radiusSm: 12,
      shadow: theme === 'dark'
        ? '0 10px 30px rgba(0,0,0,0.35)'
        : '0 10px 24px rgba(34,158,217,0.12)',
    };
    if (theme === 'dark') {
      return {
        ...common,
        // bg: '#0F141A',
        bgSoft: '#171D24',
        text: '#F4F6FA',
        textDim: 'rgba(244,246,250,0.7)',
        cardBorder: 'rgba(255,255,255,0.08)',
        cardHover: 'rgba(255,255,255,0.06)',
      };
    }
    // light
    return {
      ...common,
      bg: '#F7F9FC',
      bgSoft: '#FFFFFF',
      text: '#0B1B2B',
      textDim: 'rgba(11,27,43,0.7)',
      cardBorder: 'rgba(11,27,43,0.08)',
      cardHover: 'rgba(11,27,43,0.06)',
    };
  }, [theme]);

  const styles = {
    page: {
      minHeight: '100vh',
      background: colors.bg,
      color: colors.text,
      transition: 'background 200ms ease, color 200ms ease',
    },
    wrap: {
      width: '100%',
      maxWidth: 1280,
      margin: '0 auto',
      padding: '48px 24px 96px',
    },
    hero: {
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center',
      textAlign: 'center',
      gap: 18,
      margin: '24px 0 40px',
    },
    heroRow: {
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'space-between',
      gap: 120,
      marginTop: 12,
      width: '100%',
      maxWidth: 1280,
    },
    heroIcon: {
      width: 320,
      height: 320,
      borderRadius: 28,
      background: colors.bgSoft,
      border: `1px solid ${colors.cardBorder}`,
      boxShadow: colors.shadow,
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
      overflow: 'hidden',
      marginRight: 100,
    },
    heroCol: {
      flex: '1 1 560px',
      maxWidth: 600,
      textAlign: 'left',
      marginLeft: 100,
    },
    eyebrow: {
      fontSize: 14,
      letterSpacing: 1,
      textTransform: 'uppercase',
      opacity: 0.75,
    },
    title: {
      fontSize: 48,
      lineHeight: 1.1,
      fontWeight: 900,
      letterSpacing: 0.2,
      maxWidth: 880,
      margin: '0 auto',
      textAlign: 'center',
    },
    subtitle: {
      fontSize: 17,
      lineHeight: 1.65,
      color: colors.textDim,
      maxWidth: 720,
      margin: '0 auto',
      textAlign: 'center',
    },
    ctaRow: {
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'flex-start',
      gap: 12,
      marginTop: 12,
    },
    btn: {
      display: 'inline-flex',
      alignItems: 'center',
      justifyContent: 'center',
      gap: 10,
      padding: '12px 18px',
      borderRadius: colors.radiusLg,
      border: `1px solid ${colors.cardBorder}`,
      background: theme === 'dark' ? 'rgba(255,255,255,0.04)' : 'rgba(34,158,217,0.06)',
      backdropFilter: 'saturate(130%) blur(4px)',
      boxShadow: colors.shadow,
      color: colors.text,
      textDecoration: 'none',
      transition: 'transform 180ms ease, box-shadow 180ms ease, background 180ms ease, border-color 180ms ease',
      cursor: 'pointer',
    },
    btnPrimary: {
      background: `linear-gradient(180deg, ${colors.accentSoft}, rgba(34,158,217,0.10))`,
      border: `1px solid ${colors.focus}`,
    },
    btnIcon: {
      width: 24,
      height: 24,
      display: 'inline-block',
      flex: '0 0 24px',
    },
    grid: {
      display: 'flex',
      flexWrap: 'wrap',
      justifyContent: 'space-between',
      gap: 16,
      marginTop: 48,
    },
    card: {
      flex: '1 1 360px',
      minWidth: 280,
      maxWidth: 410,
      padding: 24,
      borderRadius: colors.radiusLg,
      background: colors.bgSoft,
      border: `1px solid ${colors.cardBorder}`,
      boxShadow: colors.shadow,
      transition: 'transform 200ms ease, box-shadow 200ms ease, background 200ms ease, border-color 200ms ease',
    },
    cardHover: {
      transform: 'translateY(-2px)',
      boxShadow: theme === 'dark'
        ? '0 14px 36px rgba(0,0,0,0.45)'
        : '0 14px 30px rgba(34,158,217,0.16)',
      background: theme === 'dark' ? 'rgba(255,255,255,0.02)' : '#fff',
      borderColor: colors.focus,
    },
    cardHead: {
      display: 'flex',
      alignItems: 'center',
      gap: 12,
      marginBottom: 8,
    },
    cardTitle: { fontSize: 20, fontWeight: 800 },
    cardText: { color: colors.textDim, lineHeight: 1.7, fontSize: 16 },
    // маленькие иконки (SVG inline)
    iconWrap: {
      width: 40,
      height: 40,
      display: 'inline-flex',
      alignItems: 'center',
      justifyContent: 'center',
      borderRadius: 12,
      background: colors.accentSoft,
      border: `1px solid ${colors.cardBorder}`,
    },
    // разделитель
    divider: {
      height: 1,
      background: colors.cardBorder,
      margin: '56px 0 28px',
    },
    highlight: {
      display: 'inline-block',
      marginTop: 12,
      padding: '8px 14px',
      fontWeight: 800,
      background: colors.accentSoft,
      border: `1px solid ${colors.focus}`,
      borderRadius: 14,
    },
  };

  const Card = ({ icon, title, text }) => (
    <div
      style={styles.card}
      onMouseEnter={(e) => Object.assign(e.currentTarget.style, styles.cardHover)}
      onMouseLeave={(e) => {
        e.currentTarget.style.transform = '';
        e.currentTarget.style.boxShadow = colors.shadow;
        e.currentTarget.style.background = colors.bgSoft;
        e.currentTarget.style.borderColor = colors.cardBorder;
      }}
    >
      <div style={styles.cardHead}>
        <div style={styles.iconWrap}>{icon}</div>
        <div style={styles.cardTitle}>{title}</div>
      </div>
      <div style={styles.cardText}>{text}</div>
    </div>
  );

  // SVG-иконки (моно, под любой фон)
  const ShieldIcon = (
    <svg width="22" height="22" viewBox="0 0 24 24" aria-hidden="true">
      <path d="M12 3l7 3v6c0 5-3.5 7.5-7 9-3.5-1.5-7-4-7-9V6l7-3z" fill="none" stroke={colors.accent} strokeWidth="2" />
      <path d="M9 12l2 2 4-4" fill="none" stroke={colors.success} strokeWidth="2" strokeLinecap="round" />
    </svg>
  );
  const CursorIcon = (
    <svg width="22" height="22" viewBox="0 0 24 24" aria-hidden="true">
      <path d="M3 2l8 18 2-7 7-2L3 2z" fill="none" stroke={colors.accent} strokeWidth="2" strokeLinejoin="round"/>
    </svg>
  );
  const BoltIcon = (
    <svg width="22" height="22" viewBox="0 0 24 24" aria-hidden="true">
      <path d="M13 2L3 14h7l-1 8 10-12h-7l1-8z" fill="none" stroke={colors.accent} strokeWidth="2" strokeLinejoin="round"/>
    </svg>
  );
  const GearIcon = (
    <svg width="22" height="22" viewBox="0 0 24 24" aria-hidden="true">
      <circle cx="12" cy="12" r="3.5" fill="none" stroke={colors.accent} strokeWidth="2"/>
      <path d="M19 12a7 7 0 0 0-.2-1.7l2.1-1.6-2-3.5-2.6 1a7 7 0 0 0-2.2-1.2L13 1h-2l-.2 2a7 7 0 0 0-2.2 1.2l-2.6-1-2 3.5 2.1 1.6A7 7 0 0 0 5 12c0 .6.1 1.1.2 1.7l-2.1 1.6 2 3.5 2.6-1c.7.5 1.4.9 2.2 1.2l.2 2h2l.2-2c.8-.3 1.5-.7 2.2-1.2l2.6 1 2-3.5-2.1-1.6c.1-.6.2-1.1.2-1.7z" fill="none" stroke={colors.accent} strokeWidth="2"/>
    </svg>
  );
  const HeadsetIcon = (
    <svg width="22" height="22" viewBox="0 0 24 24" aria-hidden="true">
      <path d="M4 12a8 8 0 1 1 16 0v5a2 2 0 0 1-2 2h-2" fill="none" stroke={colors.accent} strokeWidth="2"/>
      <rect x="3" y="12" width="4" height="7" rx="2" fill="none" stroke={colors.accent} strokeWidth="2"/>
      <rect x="17" y="12" width="4" height="7" rx="2" fill="none" stroke={colors.accent} strokeWidth="2"/>
    </svg>
  );
  const TgIcon = (
    <svg width="24" height="24" viewBox="0 0 24 24" aria-hidden="true">
      <path d="M21 3L3 11l5 2 2 5 3-4 4 3 4-14z" fill="none" stroke={colors.accent} strokeWidth="2" strokeLinejoin="round"/>
    </svg>
  );
  const DocIcon = (<svg width="24" height="24"></svg>); // TODO: replace with minimalistic icon

  return (
    <div style={styles.page}>
      <div style={{
        ...styles.wrap,
        opacity: fading ? 0 : 1,
        transform: fading ? 'translateY(4px)' : 'none',
        transition: 'opacity 200ms ease, transform 200ms ease'
      }}>
        {/* HERO */}
        <section style={styles.hero}>
          <div style={styles.heroRow}>
            <div style={styles.heroCol}>
              <div style={{ ...styles.eyebrow, textAlign: 'left', margin: '0 0 6px' }}>
                {t.eyebrow}
              </div>
              <h1 style={{ ...styles.title, textAlign: 'left', margin: 0 }}>
                {String(t.title).split('\n').map((line, i, arr) => (
                  <React.Fragment key={i}>
                    {line}
                    {i < arr.length - 1 && <br/>}
                  </React.Fragment>
                ))}
              </h1>
              <p style={{ ...styles.subtitle, textAlign: 'left', margin: '14px 0 0' }}>
                {t.subtitle}
              </p>
              <div style={styles.ctaRow}>
                <a
                  href="https://t.me/bezdelniki4"
                  style={{ ...styles.btn, ...styles.btnPrimary, width: 300, justifyContent: 'center' }}
                  onMouseEnter={(e)=>Object.assign(e.currentTarget.style, { transform: 'translateY(-1px)', boxShadow: '0 12px 28px rgba(34,158,217,0.22)' })}
                  onMouseLeave={(e)=>{ e.currentTarget.style.transform=''; e.currentTarget.style.boxShadow=colors.shadow; }}
                >
                  {TgIcon}
                  {t.ctaTelegram}
                </a>
                <a
                  href="/agreement"
                  style={{ ...styles.btn, width: 300, justifyContent: 'center' }}
                  onMouseEnter={(e)=>Object.assign(e.currentTarget.style, { transform: 'translateY(-1px)' })}
                  onMouseLeave={(e)=>{ e.currentTarget.style.transform=''; }}
                >
                  {DocIcon}
                  {t.ctaAgreement}
                </a>
              </div>
            </div>
            
            <div style={{display: 'flex', justifyContent: 'space-around'}}>
              <img src="/access/main.png" alt="hero" style={{width: '500px', height: '100%', objectFit: 'cover'}}/>
              {/* <svg width="88" height="88" viewBox="0 0 24 24" aria-hidden="true">
                <path d="M12 3l7 3v6c0 5-3.5 7.5-7 9-3.5-1.5-7-4-7-9V6l7-3z" fill="none" stroke={colors.accent} strokeWidth="2" />
                <path d="M9 12l2 2 4-4" fill="none" stroke={colors.success} strokeWidth="2" strokeLinecap="round" />
              </svg> */}
            </div>

          </div>
        </section>

        <div style={styles.divider} />

        {/* FEATURES */}
        <section>
          <div style={{ textAlign: 'center', marginBottom: 18, maxWidth: 880, marginInline: 'auto' }}>
            <h2 style={{ fontSize: 28, margin: 0, fontWeight: 800 }}>{t.featuresTitle}</h2>
            <div style={{ ...styles.subtitle, marginTop: 10 }}>
              {t.featuresSubtitle}
            </div>
            <div style={styles.highlight}>{t.highlight}</div>
          </div>

          <div style={styles.grid}>
            {t.cards && t.cards.length ? (
              t.cards.map((c, idx) => (
                <Card
                  key={idx}
                  icon={[ShieldIcon, CursorIcon, HeadsetIcon, GearIcon, BoltIcon, CursorIcon][idx] || CursorIcon}
                  title={c.title}
                  text={c.text}
                />
              ))
            ) : null}
          </div>
        </section>
      </div>
    </div>
  );
}
