import React, { useState, useEffect, useRef } from 'react';
import { NavLink } from 'react-router-dom';

const languageOptions = [
  { code: 'ru', label: 'Русский' },
  { code: 'en', label: 'English' },
  // { code: 'uk', label: 'Українська' },
  { code: 'kk', label: 'Қазақша' },
  { code: 'uz', label: 'Oʻzbekcha' },
  { code: 'de', label: 'Deutsch' },
  { code: 'fr', label: 'Français' },
  { code: 'es', label: 'Español' },
  { code: 'hi', label: 'हिन्दी' },
  { code: 'tr', label: 'Türkçe' },
  { code: 'ar', label: 'العربية' },
  { code: 'zh', label: '中文' },
];

const Navbar = ({ lang, setLang, theme, setTheme }) => {
  const labels = lang === 'ru'
    ? { home: 'Главная', games: 'Игры', programs: 'Программы', scripts: 'Скрипты', contacts: 'Контакты', agreements: 'Соглашение', download: 'Скачать', language: 'Язык', theme: 'Тема' }
    : lang === 'uk'
    ? { home: 'Головна', games: 'Ігри', programs: 'Програми', scripts: 'Скрипти', contacts: 'Контакти', agreements: 'Угода', download: 'Завантажити', language: 'Мова', theme: 'Тема' }
    : lang === 'kk'
    ? { home: 'Басты бет', games: 'Ойындар', programs: 'Бағдарламалар', scripts: 'Скрипттер', contacts: 'Байланыс', agreements: 'Келісім', download: 'Жүктеу', language: 'Тіл', theme: 'Тақырып' }
    : lang === 'uz'
    ? { home: 'Bosh sahifa', games: 'Oʻyinlar', programs: 'Dasturlar', scripts: 'Skriptlar', contacts: 'Aloqa', agreements: 'Kelib chiqish', download: 'Yuklab olish', language: 'Til', theme: 'Mavzu' }
    : lang === 'de'
    ? { home: 'Startseite', games: 'Spiele', programs: 'Programme', scripts: 'Skripte', contacts: 'Kontakt', agreements: 'Vereinbarung', download: 'Herunterladen', language: 'Sprache', theme: 'Thema' }
    : lang === 'fr'
    ? { home: 'Accueil', games: 'Jeux', programs: 'Programmes', scripts: 'Scripts', contacts: 'Contacts', agreements: 'Accord', download: 'Télécharger', language: 'Langue', theme: 'Thème' }
    : lang === 'es'
    ? { home: 'Inicio', games: 'Juegos', programs: 'Programas', scripts: 'Scripts', contacts: 'Contactos', agreements: 'Acuerdo', download: 'Descargar', language: 'Idioma', theme: 'Tema' }
    : lang === 'hi'
    ? { home: 'मुख्य पृष्ठ', games: 'खेल', programs: 'कार्यक्रम', scripts: 'स्क्रिप्ट', contacts: 'संपर्क', agreements: 'समझौता', download: 'डाउनलोड', language: 'भाषा', theme: 'थीम' }
    : lang === 'tr'
    ? { home: 'Ana Sayfa', games: 'Oyunlar', programs: 'Programlar', scripts: 'Betikler', contacts: 'İletişim', agreements: 'Anlaşma', download: 'İndir', language: 'Dil', theme: 'Tema' }
    : lang === 'ar'
    ? { home: 'الرئيسية', games: 'الألعاب', programs: 'البرامج', scripts: 'السكريبتات', contacts: 'الاتصال', agreements: 'الاتفاقية', download: 'تحميل', language: 'اللغة', theme: 'السمة' }
    : lang === 'zh'
    ? { home: '首页', games: '游戏', programs: '程序', scripts: '脚本', contacts: '联系', agreements: '协议', download: '下载', language: '语言', theme: '主题' }
    : { home: 'Home', games: 'Games', programs: 'Programs', scripts: 'Scripts', contacts: 'Contacts', agreements: 'Agreement', download: 'Download', language: 'Language', theme: 'Theme' };

  const isLight = theme === 'light';

  const [langMenuOpen, setLangMenuOpen] = useState(false);
  const langMenuRef = useRef(null);

  React.useEffect(() => {
    function handleClickOutside(event) {
      if (langMenuRef.current && !langMenuRef.current.contains(event.target)) {
        setLangMenuOpen(false);
      }
    }
    if (langMenuOpen) {
      document.addEventListener('mousedown', handleClickOutside);
    } else {
      document.removeEventListener('mousedown', handleClickOutside);
    }
    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, [langMenuOpen]);

  React.useEffect(() => {
    localStorage.setItem('theme', theme);
    if (typeof window !== 'undefined') {
      window.dispatchEvent(new CustomEvent('app-theme-changed', { detail: theme }));
      document.documentElement.setAttribute('data-theme', theme);
      document.body.setAttribute('data-theme', theme);
    }
  }, [theme]);

  React.useEffect(() => {
    if (typeof window !== 'undefined') {
      const saved = localStorage.getItem('theme');
      const current = saved || theme || 'light';
      window.dispatchEvent(new CustomEvent('app-theme-changed', { detail: current }));
      document.documentElement.setAttribute('data-theme', current);
      document.body.setAttribute('data-theme', current);
    }
    // run once on mount
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  React.useEffect(() => {
    localStorage.setItem('lang', lang);
  }, [lang]);

  const styles = {
    wrap: {
      position: 'sticky',
      top: 0,
      zIndex: 50,
      backdropFilter: 'saturate(1.2) blur(8px)',
      background: isLight ? 'rgba(255, 255, 255, 0.85)' : 'rgba(13, 17, 23, 0.75)',
      borderBottom: isLight ? '1px solid rgba(0,0,0,0.06)' : '1px solid rgba(255,255,255,0.08)'
    },
    bar: {
      maxWidth: 1280,
      margin: '0 auto',
      padding: '18px 28px 18px 0',
      minHeight: 72,
      display: 'grid',
      gridTemplateColumns: '1fr auto 1fr',
      justifyContent: 'space-between',
      alignItems: 'center',
      gap: 24,
      paddingRight: 16,
    },
    telegramBtnRight: {
      height: 38,
      borderRadius: 10,
      border: isLight ? '1px solid rgba(0,0,0,0.2)' : '1px solid rgba(255,255,255,0.2)',
      color: isLight ? 'rgba(0,0,0,0.7)' : 'rgba(255,255,255,0.7)',
      cursor: 'pointer',
      boxShadow: 'none',
      display: 'inline-flex',
      alignItems: 'center',
      justifyContent: 'center',
      userSelect: 'none',
      outline: 'none',
      lineHeight: 1,
      gap: 6,
      padding: '0 14px',
      background: 'transparent',
      textDecoration: 'none',
      fontWeight: 600,
      fontSize: 14,
      transition: 'box-shadow 200ms ease, transform 200ms ease',
    },
    telegramBtnRightHover: {
      boxShadow: '0 0 8px rgba(34,158,217,0.4)',
      transform: 'scale(1.03)',
    },
    brand: {
      display: 'inline-flex',
      alignItems: 'center',
      gap: 12,
      color: isLight ? '#0b1324' : '#e5e7eb',
      textDecoration: 'none',
      fontWeight: 800,
      letterSpacing: 0.2,
      fontSize: 20,
      whiteSpace: 'nowrap',
      justifySelf: 'start',
    },
    brandIcon: {
      width: 24,
      height: 24,
      display: 'inline-block',
      borderRadius: 6,
      background: 'linear-gradient(135deg, #2563eb, #06b6d4)',
      boxShadow: '0 8px 24px rgba(2,132,199,0.35)'
    },
    centerMenu: {
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
      gap: 32,
      justifySelf: 'center',
    },
    link: (active) => ({
      padding: '6px 0',
      fontSize: 15,
      fontWeight: active ? 700 : 600,
      color: isLight ? '#0f172a' : active ? '#ffffff' : '#d1d5db',
      textDecoration: 'none',
      borderBottom: active ? '2px solid ' + (isLight ? '#0f172a' : '#ffffff') : '2px solid transparent',
      transition: 'border-color 160ms ease, opacity 160ms ease',
      opacity: active ? 1 : 0.75,
      cursor: 'pointer',
      lineHeight: '1.6',
    }),
    right: {
      display: 'flex',
      justifyContent: 'flex-end',
      alignItems: 'center',
      gap: 8,
      position: 'relative',
      justifySelf: 'end',
    },
    chip: {
      padding: '0 14px',
      height: 38,
      borderRadius: 10,
      background: 'transparent',
      border: isLight ? '1px solid rgba(0,0,0,0.2)' : '1px solid rgba(255,255,255,0.2)',
      color: isLight ? 'rgba(0,0,0,0.7)' : 'rgba(255,255,255,0.7)',
      fontSize: 13,
      letterSpacing: 0.3,
      cursor: 'pointer',
      boxShadow: 'none',
      userSelect: 'none',
      outline: 'none',
      display: 'inline-flex',
      alignItems: 'center',
      justifyContent: 'center',
      position: 'relative',
      lineHeight: 1,
    },
    iconBtn: {
      width: 38,
      height: 38,
      borderRadius: 10,
      background: 'transparent',
      border: isLight ? '1px solid rgba(0,0,0,0.2)' : '1px solid rgba(255,255,255,0.2)',
      color: isLight ? 'rgba(0,0,0,0.7)' : 'rgba(255,255,255,0.7)',
      cursor: 'pointer',
      boxShadow: 'none',
      display: 'inline-flex',
      alignItems: 'center',
      justifyContent: 'center',
      userSelect: 'none',
      outline: 'none',
      lineHeight: 1,
    },
    langMenu: {
      position: 'absolute',
      top: 'calc(100% + 8px)',
      right: 0,
      background: isLight ? 'white' : 'rgba(20,20,20,0.95)',
      borderRadius: 10,
      border: isLight ? '1px solid rgba(0,0,0,0.2)' : '1px solid rgba(255,255,255,0.2)',
      boxShadow: isLight
        ? '0 4px 12px rgba(0,0,0,0.1)'
        : '0 4px 12px rgba(0,0,0,0.7)',
      opacity: langMenuOpen ? 1 : 0,
      transform: langMenuOpen ? 'translateY(0)' : 'translateY(-10px)',
      pointerEvents: langMenuOpen ? 'auto' : 'none',
      transition: 'opacity 200ms ease, transform 200ms ease',
      zIndex: 100,
      minWidth: 140,
      userSelect: 'none',
    },
    langMenuItem: {
      padding: '10px 16px',
      fontSize: 14,
      color: isLight ? 'rgba(0,0,0,0.8)' : 'rgba(255,255,255,0.8)',
      cursor: 'pointer',
      transition: 'background-color 150ms ease, color 150ms ease',
      borderRadius: 8,
      userSelect: 'none',
    },
    langMenuItemHover: {
      backgroundColor: isLight ? 'rgba(0,0,0,0.05)' : 'rgba(255,255,255,0.1)',
      color: isLight ? 'rgba(0,0,0,0.95)' : 'rgba(255,255,255,0.95)',
    }
  };

  const [hoveredLangIndex, setHoveredLangIndex] = useState(null);
  const [telegramHoverRight, setTelegramHoverRight] = useState(false);

  return (
    <div style={styles.wrap}>
      <div style={styles.bar}>
        {/* Brand */}
        <NavLink to="/" style={styles.brand}>
          <img src='/access/icon.png' style={{float: 'right', width: 200, height:120}} />
        </NavLink>

        {/* Center: Menu */}
        <nav style={styles.centerMenu} aria-label="Main navigation">
          <NavLink to="/" style={({isActive}) => styles.link(isActive)}>{labels.home}</NavLink>
          <NavLink to="/game" style={({isActive}) => styles.link(isActive)}>{labels.games}</NavLink>
          <NavLink to="/program" style={({isActive}) => styles.link(isActive)}>{labels.programs}</NavLink>
          {/* <NavLink to="/script" style={({isActive}) => styles.link(isActive)}>{labels.scripts}</NavLink> */}
          <NavLink to="/contact" style={({isActive}) => styles.link(isActive)}>{labels.contacts}</NavLink>
          <NavLink to="/agreement" style={({isActive}) => styles.link(isActive)}>{labels.agreements}</NavLink>
        </nav>

        {/* Right: Telegram, Language, Theme */}
        <div style={styles.right} ref={langMenuRef}>
          <a
            href="https://t.me/bezdelniki4"
            target="_blank"
            rel="noopener noreferrer"
            style={{
              ...styles.telegramBtnRight,
              ...(telegramHoverRight ? styles.telegramBtnRightHover : {})
            }}
            onMouseEnter={() => setTelegramHoverRight(true)}
            onMouseLeave={() => setTelegramHoverRight(false)}
            aria-label="Telegram"
            title="Telegram"
          >
            <img
              src="https://upload.wikimedia.org/wikipedia/commons/8/82/Telegram_logo.svg"
              alt="Telegram"
              height={18}
              width={18}
              draggable="false"
              style={{display: 'block'}}
            />
          </a>

          <button
            style={styles.chip}
            onClick={() => setLangMenuOpen(!langMenuOpen)}
            aria-label={labels.language}
            title={labels.language}
            type="button"
          >
            {languageOptions.find(o => o.code === lang)?.label || 'Lang'}
            <svg style={{marginLeft: 8, width: 10, height: 10, fill: isLight ? 'rgba(0,0,0,0.6)' : 'rgba(255,255,255,0.6)'}} viewBox="0 0 20 20"><path d="M5 7l5 5 5-5H5z"/></svg>
          </button>
          <div style={styles.langMenu} role="menu" aria-label={labels.language}>
            {languageOptions.map((option, i) => (
              <div
                key={option.code}
                role="menuitem"
                tabIndex={0}
                onClick={() => { setLang(option.code); setLangMenuOpen(false); }}
                onKeyDown={e => { if(e.key === 'Enter' || e.key === ' ') { setLang(option.code); setLangMenuOpen(false); e.preventDefault(); } }}
                onMouseEnter={() => setHoveredLangIndex(i)}
                onMouseLeave={() => setHoveredLangIndex(null)}
                style={{
                  ...styles.langMenuItem,
                  ...(hoveredLangIndex === i ? styles.langMenuItemHover : {})
                }}
              >
                {option.label}
              </div>
            ))}
          </div>
          <button
            style={styles.iconBtn}
            onClick={() => setTheme(theme === 'light' ? 'dark' : 'light')}
            aria-label={labels.theme}
            title={labels.theme}
            type="button"
          >
            {theme === 'light' ? '☀️' : '🌙'}
          </button>
        </div>
      </div>
    </div>
  );
};

export default Navbar;
