// Full-screen screen for phones: block access (no overlays, standalone screen)
function OnlyPCScreen({ theme, TT }) {
  return (
    <div style={{
      position: 'fixed', inset: 0, zIndex: 2250,
      display: 'flex', alignItems: 'center', justifyContent: 'center',
      background: theme === 'dark' ? '#0B0F14' : '#F7F9FC',
    }}>
      <div style={{
        display: 'flex', flexDirection: 'column', alignItems: 'center', justifyContent: 'center',
        gap: 16, textAlign: 'center', padding: 24
      }}>
        <lottie-player
          autoplay
          loop
          mode="normal"
          src="/access/ONLYPC.json"
          style={{ width: 280, height: 280 }}
        ></lottie-player>
        <div style={{ fontWeight: 900, fontSize: 24, color: theme === 'dark' ? '#F4F6FA' : '#0B1B2B' }}>
          {TT.onlyPcTitle}
        </div>
        <div style={{ maxWidth: 560, fontSize: 14, lineHeight: 1.6, opacity: 0.85, color: theme === 'dark' ? 'rgba(244,246,250,0.85)' : 'rgba(11,27,43,0.8)' }}>
          {TT.onlyPcSubtitle}
        </div>
      </div>
    </div>
  );
}

// Gate: if phone => OnlyPCScreen, else render the site
function DeviceGate({ isPhone, theme, children, TT }) {
  if (isPhone) return <OnlyPCScreen theme={theme} TT={TT} />;
  return <>{children}</>;
}

function App() {
  const [lang, setLang] = useState(() => {
    try {
      const saved = localStorage.getItem('lang');
      if (saved && SUPPORTED_LANGS.includes(saved)) return saved;
      const nav = (navigator.language || navigator.userLanguage || 'ru').slice(0,2).toLowerCase();
      return SUPPORTED_LANGS.includes(nav) ? nav : 'ru';
    } catch { return 'ru'; }
  });
  const [theme, setTheme] = useState(() => {
    const storedTheme = localStorage.getItem('theme');
    if (storedTheme) return storedTheme;
    return 'light';
  });
  const [isPhone, setIsPhone] = useState(() => {
    try {
      const ua = (navigator.userAgent || '').toLowerCase();
      const isIPhone = /iphone/.test(ua);
      const isAndroidPhone = /android/.test(ua) && /mobile/.test(ua);
      const isWindowsPhone = /windows phone/.test(ua);
      const w = (typeof window !== 'undefined') ? (window.innerWidth || document.documentElement.clientWidth || 0) : 0;
      return (isIPhone || isAndroidPhone || isWindowsPhone) || w < 720;
    } catch { return false; }
  });

  const TT = (APP_TEXTS[lang] || APP_TEXTS.ru || APP_TEXTS.en);

  const [isLoading, setIsLoading] = useState(true);
  const [loadProgress, setLoadProgress] = useState(0);
  const preloadAbort = useRef(false);
  const [showTgPrompt, setShowTgPrompt] = useState(() => {
    try { return !localStorage.getItem('tg_prompt_shown'); } catch { return true; }
  });

  // Prevent runtime crash when third-party code reads xhr.responseText while responseType==='json'
  useEffect(() => {
    try {
      const proto = typeof XMLHttpRequest !== 'undefined' ? XMLHttpRequest.prototype : null;
      if (proto && !proto.__rtSafe) {
        const desc = Object.getOwnPropertyDescriptor(proto, 'responseText');
        if (desc && typeof desc.get === 'function') {
          Object.defineProperty(proto, 'responseText', {
            configurable: true,
            enumerable: desc.enumerable,
            get: function () {
              try {
                const rt = this && this.responseType;
                if (rt && rt !== '' && rt !== 'text') {
                  const r = this.response;
                  if (r == null) return '{}';
                  if (typeof r === 'string') return r === 'null' ? '{}' : r;
                  let s = '{}';
                  try { s = JSON.stringify(r) || '{}'; } catch (_) {}
                  return s === 'null' ? '{}' : s;
                }
                return desc.get.call(this);
              } catch (_) {
                return '{}';
              }
            }
          });
          Object.defineProperty(proto, '__rtSafe', { value: true });
        }
      }
    } catch (_) {}
  }, []);

  useEffect(() => {
    // Mirror to <html>/<body> and notify listeners (contact/agreement pages, etc.)
    if (typeof window !== 'undefined') {
      document.documentElement.setAttribute('data-theme', theme);
      document.body.setAttribute('data-theme', theme);
      window.dispatchEvent(new CustomEvent('app-theme-changed', { detail: theme }));
    }
  }, [theme]);

  useEffect(() => {
    const compute = () => {
      const ua = (navigator.userAgent || '').toLowerCase();
      const isIPhone = /iphone/.test(ua);
      const isAndroidPhone = /android/.test(ua) && /mobile/.test(ua);
      const isWindowsPhone = /windows phone/.test(ua);
      const phone = isIPhone || isAndroidPhone || isWindowsPhone;
      const width = Math.min(window.innerWidth || 0, document.documentElement.clientWidth || 0);
      setIsPhone(phone || width < 720);
    };
    compute();
    window.addEventListener('resize', compute);
    window.addEventListener('orientationchange', compute);
    return () => {
      window.removeEventListener('resize', compute);
      window.removeEventListener('orientationchange', compute);
    };
  }, []);

  useEffect(() => {
    localStorage.setItem('lang', lang);
  }, [lang]);

  useEffect(() => {
    localStorage.setItem('theme', theme);
  }, [theme]);

  useEffect(() => {
    let mounted = true;
    const inc = (i, total) => mounted && setLoadProgress(Math.round(((i) / total) * 100));
    const loadImage = (src) => new Promise((resolve) => {
      const img = new Image();
      img.onload = img.onerror = () => resolve(src);
      img.src = src;
    });

    // Static list of images to warm up (no fetches, no json)
    const assets = [
      '/access/main.png',
      '/access/memez-preview.webp',
      '/access/unicore-preview.png',
      '/access/solara-preview.png',
      '/access/midnight-preview.png'
    ];

    const start = async () => {
      const total = assets.length || 1;
      let done = 0;

      // Light imitation: begin at 5%, then progress as images load
      setLoadProgress(5);

      for (const a of assets) {
        if (preloadAbort.current) break;
        try {
          await loadImage(a);
        } finally {
          done += 1; inc(done, total);
        }
      }

      // Ease-out finish
      if (mounted) {
        let current = 70;
        const tick = () => {
          current += 6;
          if (current >= 100) {
            setLoadProgress(100);
            setTimeout(() => setIsLoading(false), 150);
          } else {
            setLoadProgress(current);
            setTimeout(tick, 60);
          }
        };
        setTimeout(tick, 120);
      }
    };

    start();
    return () => { mounted = false; preloadAbort.current = true; };
  }, []);

  const closeTgPrompt = (persist = false) => {
    setShowTgPrompt(false);
    if (persist) {
      try { localStorage.setItem('tg_prompt_shown', '1'); } catch {}
    }
  };

  const isLight = theme === 'light';

  const appStyles = {
    root: {
      minHeight: 'max(100vh, 720px)',
      background: isLight
        ? '#f7f9fc'
        : 'linear-gradient(180deg, #0b0f14 0%, #0f1720 40%, #0b0f14 100%)',
      color: isLight ? '#111827' : '#e5e7eb',
      fontFamily: 'ui-sans-serif, system-ui, -apple-system, Segoe UI, Roboto, Ubuntu, Cantarell, Noto Sans, Helvetica Neue, Arial, "Apple Color Emoji", "Segoe UI Emoji"',
      display: 'flex',
      flexDirection: 'column',
    },
    main: {
      flex: 1,
      minHeight: 720,
      padding: '24px 0',
    },
    container: {
      width: '100%',
      maxWidth: 1280,
      margin: '0 auto',
      padding: '24px 28px',
      fontSize: 18,
    }
  };


  return (
    <Router>
      <DeviceGate isPhone={isPhone} theme={theme} TT={TT}>
        <div style={appStyles.root}>
          <Navbar lang={lang} setLang={setLang} theme={theme} setTheme={setTheme} />
          {isLoading && (
            <div style={{
              position: 'fixed', inset: 0, zIndex: 2000,
              display: 'flex', alignItems: 'center', justifyContent: 'center',
              background: theme === 'dark' ? 'rgba(10,12,16,0.92)' : 'rgba(247,249,252,0.92)',
              backdropFilter: 'saturate(140%) blur(6px)'
            }}>
              <div style={{ width: 320, maxWidth: '90%', textAlign: 'center' }}>
                <div style={{ fontSize: 14, fontWeight: 700, letterSpacing: 0.3, marginBottom: 10, color: theme === 'dark' ? '#E5E7EB' : '#0B1B2B' }}>
                  {TT.loader}
                </div>
                <div style={{ height: 10, borderRadius: 999, background: theme === 'dark' ? 'rgba(255,255,255,0.08)' : 'rgba(0,0,0,0.08)', overflow: 'hidden', boxShadow: theme === 'dark' ? 'inset 0 1px 2px rgba(0,0,0,0.35)' : 'inset 0 1px 2px rgba(0,0,0,0.08)' }}>
                  <div style={{
                    height: '100%', width: `${loadProgress}%`,
                    background: '#229ED9', transition: 'width 180ms ease'
                  }}/>
                </div>
                <div style={{ marginTop: 10, fontSize: 12, color: theme === 'dark' ? 'rgba(229,231,235,0.8)' : 'rgba(11,27,43,0.7)' }}>
                  {loadProgress}%
                </div>
              </div>
            </div>
          )}
          <main style={appStyles.main}>
            <Routes>
              <Route path="/" element={<Main lang={lang} />} />
              <Route path="/game" element={<Game lang={lang} />} />
              <Route path="/program" element={<Program lang={lang} />} />
              <Route path="/script" element={<Script lang={lang} />} />
              <Route path="/contact" element={<Contact lang={lang} />} />
              <Route path="/agreement" element={<AgreementPage lang={lang} />} />
            </Routes>
          </main>
          <footer style={{
            padding: '18px 24px',
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
            borderTop: theme === 'dark' ? '1px solid rgba(255,255,255,0.08)' : '1px solid rgba(0,0,0,0.08)',
            color: theme === 'dark' ? 'rgba(229,231,235,0.8)' : 'rgba(11,27,43,0.72)',
            fontSize: 13,
            letterSpacing: 0.2
          }}>
            © 2025 · Все права защищены
          </footer>
          {showTgPrompt && (
            <div
              style={{
                position: 'fixed', inset: 0, zIndex: 2100,
                background: 'rgba(0,0,0,0.45)',
                backdropFilter: 'blur(2px)'
              }}
              onClick={(e) => {
                if (e.target === e.currentTarget) closeTgPrompt(true);
              }}
            >
              <div style={{
                position: 'absolute', left: '50%', top: 'calc(50% - 20px)', transform: 'translate(-50%, -50%)',
                width: 'min(92vw, 460px)',
                background: theme === 'dark' ? '#171D24' : '#FFFFFF',
                border: theme === 'dark' ? '1px solid rgba(255,255,255,0.08)' : '1px solid rgba(0,0,0,0.08)',
                borderRadius: 16,
                boxShadow: theme === 'dark' ? '0 18px 40px rgba(0,0,0,0.5)' : '0 18px 40px rgba(0,0,0,0.08)',
                color: theme === 'dark' ? '#F4F6FA' : '#0B1B2B',
                padding: 18
              }}>
                <button onClick={() => closeTgPrompt(true)} aria-label="Закрыть"
                  style={{
                    position: 'absolute', right: 10, top: 10,
                    width: 34, height: 34, borderRadius: 999,
                    border: '1px solid rgba(0,0,0,0.08)', background: 'transparent',
                    color: 'inherit', cursor: 'pointer'
                  }}>
                  ✕
                </button>
                <div style={{alignItems: 'center', gap: 12, marginBottom: 12}} align={'center'}>
                  <lottie-player
                    autoplay
                    loop
                    mode="normal"
                    src="/access/DUCKNEWS.json"
                    style={{ width: 250 }}
                  ></lottie-player>
                  <div style={{ fontWeight: 800, fontSize: 18, marginTop: 35}} align="center">
                    {TT.subTitle}
                  </div>
                </div>
                <span style={{ fontSize: 14, opacity: 0.85, lineHeight: 1.6, marginBottom: 16 }}>
                  {TT.subBody}
                </span>
                <div style={{ display: 'flex', gap: 12, marginTop: 10 }}>
                  <a href="https://t.me/bezdelniki4" target="_blank" rel="noopener noreferrer"
                     onClick={() => closeTgPrompt(true)}
                     style={{
                       flex: 1, textAlign: 'center', padding: '12px 16px', borderRadius: 12,
                       textDecoration: 'none', fontWeight: 700,
                       background: '#229ED9', color: '#fff', boxShadow: '0 10px 24px rgba(34,158,217,0.25)'
                     }}>
                    {TT.subBtn}
                  </a>
                  {/* <button onClick={() => closeTgPrompt(true)} style={{
                    padding: '12px 16px', borderRadius: 12, border: '1px solid rgba(0,0,0,0.08)',
                    background: 'transparent', color: 'inherit', fontWeight: 600, cursor: 'pointer'
                  }}>
                    Позже
                  </button> */}
                </div>
              </div>
            </div>
          )}
        </div>
      </DeviceGate>
    </Router>
  );
}

export default App;
