import React, { useMemo, useRef } from 'react';
import gameCards, { pwTexts } from '../data/gameList'

export default function Game({ lang }) {
  // --- i18n labels ---
  const t = useMemo(() => {
    const common = {
      choose: {
        ru: 'Выберите игру',
        en: 'Select a game',
        uk: 'Оберіть гру',
        tr: 'Oyunu seçin',
        ar: 'اختر لعبة',
        zh: '选择游戏',
        kk: 'Ойынды таңдаңыз',
        uz: 'Oʻyin tanlang',
        de: 'Spiel wählen',
        fr: 'Choisissez un jeu',
        es: 'Elige un juego',
        hi: 'गेम चुनें',
      }[lang] || 'Select a game',
      download: {
        ru: 'Скачать', en: 'Download', uk: 'Завантажити', tr: 'İndir', ar: 'تحميل', zh: '下载',
        kk: 'Жүктеу', uz: 'Yuklab olish', de: 'Herunterladen', fr: 'Télécharger', es: 'Descargar', hi: 'डाउनलोड'
      }[lang] || 'Download',
    };
    const name = (ru, en = ru) => ({ ru, en, uk: ru, tr: en, ar: en, zh: en, kk: ru, uz: en, de: en, fr: en, es: en, hi: en }[lang] || en);
    return {
      page: { ru: 'Игры', en: 'Games', uk: 'Ігри' }[lang] || 'Games',
      choose: common.choose,
      download: common.download,
      games: {
        cs2: name('CS2'),
        // dota2: name('Dota 2'),
        roblox: name('Roblox'),
        fortnite: name('Fortnite'),
        genshin: name('Genshin Impact'),
        zzz: name('Zenless Zone Zero'),
        rust: name('Rust'),
        valorant: name('Valorant'),
        cod: name('Call of Duty'),
        warzone: name('Warzone'),
        pubg: name('PUBG'),
        battlefield: name('Battlefield'),
        lol: name('League of Legends'),
        ow2: name('Overwatch 2'),
        amongus: name('Among Us'),
        tf2: name('Team Fortress 2'),
        samp: name('SAMP'),
      },
    };
  }, [lang]);

  // i18n text picker for objects like {ru: "...", en: "..."}
  const tx = (m) => (m && typeof m === 'object') ? (m[lang] || m.en || Object.values(m)[0]) : m;

  // --- THEME (sync with Navbar data-theme) ---
  const [theme, setTheme] = React.useState(
    typeof document !== 'undefined'
      ? document.body.getAttribute('data-theme') || 'light'
      : 'light'
  );
  React.useEffect(() => {
    const handler = (e) => setTheme(e.detail);
    window.addEventListener('app-theme-changed', handler);
    return () => window.removeEventListener('app-theme-changed', handler);
  }, []);
  const isDark = theme === 'dark';

  // --- Download password modal ---
  const [pwOpen, setPwOpen] = React.useState(false);
  const [pwHref, setPwHref] = React.useState(null);
  const PW = {
    title: tx(pwTexts.title),
    note: tx(pwTexts.note),
    pass: '111',
    copy: tx(pwTexts.copy),
    cont: tx(pwTexts.cont),
    close: tx(pwTexts.close),
  };
  const onDownloadClick = (href) => {
    if (!href) return;
    setPwHref(href);
    setPwOpen(true);
  };

  // Turn absolute file paths or relative asset paths into web URLs (served from /public)
  const resolveAsset = (p) => {
    if (!p) return p;
    // External or data URLs — leave as is
    if (p.startsWith('http://') || p.startsWith('https://') || p.startsWith('data:')) return p;
    // If given a full local path, extract part after "/public/"
    const idx = p.indexOf('/public/');
    if (idx !== -1) {
      const rel = p.substring(idx + '/public'.length + 1); // skip trailing slash
      return '/' + rel.replace(/^\/+/, '');
    }
    // "./access/file.jpg" → "/access/file.jpg"
    if (p.startsWith('./')) return '/' + p.replace(/^\.\/+/, '');
    // Already site-absolute (e.g., "/access/file.jpg")
    if (p.startsWith('/')) return p;
    // Bare relative like "access/file.jpg" → "/access/file.jpg"
    return '/' + p;
  };

  // --- Game list with icon URLs (unified with per-card scale) ---
  const games = [
    { key: 'cs2', img: 'https://cdn2.steamgriddb.com/icon/e1bd06c3f8089e7552aa0552cb387c92/32/512x512.png', scale: 0.7 },
    // { key: 'dota2', img: 'https://cdn-icons-png.flaticon.com/512/588/588267.png', scale: 0.7 },
    { key: 'roblox', img: 'https://upload.wikimedia.org/wikipedia/commons/thumb/7/70/Roblox_Corporation_2025_logo.svg/250px-Roblox_Corporation_2025_logo.svg.png', scale: 0.7 },
    { key: 'fortnite', img: 'https://cdn2.steamgriddb.com/icon/7d3c92fe608388da63fa395ebe233316.png', scale: 1.1 },
    { key: 'genshin', img: 'https://cdn2.steamgriddb.com/icon/ffbab8235ddc5c0290ecd6ceccc0a61a.png', scale: 0.7 },
    { key: 'zzz', img: 'https://upload.wikimedia.org/wikipedia/en/9/92/Zenless_Zone_Zero_logo.png', scale: 0.65 },
    { key: 'rust', img: 'https://cdn2.steamgriddb.com/icon/c291b01517f3e6797c774c306591cc32.png', scale: 0.7 },
    { key: 'valorant', img: 'https://static.vecteezy.com/system/resources/thumbnails/022/636/384/small/valorant-logo-valorant-icon-transparent-free-png.png', scale: 1 },
    { key: 'pubg', img: 'https://www.rw-designer.com/icon-image/21520-256x256x32.png', scale: 0.7 },
    { key: 'tf2', img: 'https://cdn.worldvectorlogo.com/logos/team-fortress.svg', scale: 0.7 },
    { key: 'samp', img: 'https://papik.pro/grafic/uploads/posts/2023-04/1681619882_papik-pro-p-samp-logotip-vektor-28.png', scale: 0.8 },
  ];

  /*
   * --- Content cards per game ---
   * Add new cards here. Each item:
   * { id, img, image1, image2, image3, title, description, href, video? }
   * image1/image2/image3 — три ссылки на фото для слайдера (если не заданы, возьмём img три раза).
   */

  

  // refs for smooth scroll (single hook container)
  const sectionRefs = useRef({});
  const scrollTo = (key) => {
    const el = sectionRefs.current[key];
    if (el) el.scrollIntoView({ behavior: 'smooth', block: 'start' });
  };

  // --- styles ---
  const styles = {
    page: { width: '100%', maxWidth: 1280, margin: '0 auto', padding: '24px 28px 60px' },
    headerRow: { display: 'flex', alignItems: 'baseline', justifyContent: 'space-between', gap: 16, marginBottom: 16 },
    h1: { fontSize: 18, fontWeight: 700, opacity: 0.9 },
    sub: { fontSize: 14, opacity: 0.7 },

    grid: { display: 'grid', gridTemplateColumns: 'repeat(4, minmax(220px, 1fr))', gap: 16, marginTop: 10, marginBottom: 32 },
    card: {
      position: 'relative', height: 300, width: 300, borderRadius: 16, overflow: 'hidden',
      border: '1px solid rgba(128,128,128,0.25)', background: 'linear-gradient(180deg, rgba(255,255,255,0.02), rgba(0,0,0,0.2))', cursor: 'pointer',
      transition: 'transform 160ms ease, box-shadow 160ms ease, border-color 160ms ease',
    },
    cardHover: { transform: 'translateY(-3px)', boxShadow: '0 10px 28px rgba(0,0,0,0.18)', borderColor: 'rgba(128,128,128,0.35)' },

    imgWrap: { position: 'absolute', inset: 0, overflow: 'hidden' },
    img: { width: '100%', height: '100%', objectFit: 'cover', transition: 'transform 160ms ease', filter: 'brightness(0.9)' },

    title: {
      position: 'absolute', left: 0, right: 0, bottom: 0, padding: '16px 18px', fontWeight: 800, fontSize: 18, letterSpacing: 0.2, color: '#fff',
      backgroundImage: [
        'linear-gradient(to top, rgba(0,0,0,0.34) 0%, rgba(0,0,0,0.26) 20%, rgba(0,0,0,0.16) 45%, rgba(0,0,0,0.08) 70%, rgba(0,0,0,0) 100%)',
        'radial-gradient(120% 60% at 50% 120%, rgba(0,0,0,0.18), rgba(0,0,0,0) 60%)'
      ].join(','),
      textShadow: '0 1px 8px rgba(0,0,0,0.35)',
    },

    // sections below (placeholder targets for scroll)
    section: { scrollMarginTop: 96, marginBottom: 48 },
    sectionTitle: { fontSize: 24, fontWeight: 800, margin: '0 0 16px' },
    separatorWrap: { position: 'relative', height: 32, margin: '32px 0 56px' },
    separatorGlow: {
      position: 'absolute', left: 20, right: 20, top: 7,
      height: 18,
      background: 'radial-gradient(60% 100% at 50% 50%, rgba(255,255,255,0.20), rgba(255,255,255,0) 70%)',
      filter: 'blur(6px)', opacity: 0.35, pointerEvents: 'none'
    },
    separatorLine: {
      position: 'absolute', left: 20, right: 20, top: 16,
      height: 1,
      background: 'linear-gradient(90deg, rgba(255,255,255,0) 0%, rgba(180,180,180,0.45) 18%, rgba(180,180,180,0.55) 50%, rgba(180,180,180,0.45) 82%, rgba(255,255,255,0) 100%)',
      borderRadius: 1
    },

    // content cards (one per row)
    contentGrid: { display: 'grid', gridTemplateColumns: '1fr', gap: 18 },
    contentCard: {
      height: '100%',
      border: isDark ? '1px solid rgba(128,128,128,0.25)' : '1px solid rgba(0,0,0,0.08)',
      borderRadius: 16,
      overflow: 'hidden',
      background: isDark ? 'rgba(17, 24, 39, 0.25)' : '#ffffff',
      boxShadow: isDark ? '0 6px 18px rgba(0,0,0,0.12)' : '0 6px 18px rgba(0,0,0,0.06)',
      width: 'calc(100% - 160px)',
      margin: '0 auto',
    },
    contentImg: { width: '100%', height: 'var(--card-img-height)', objectFit: 'cover' },
    // --- Per-card carousel styles ---
    contentImgBox: { position: 'relative', width: '100%', height: 'var(--card-img-height)', overflow: 'hidden' },
    carouselImg: {
      position: 'absolute', inset: 0,
      width: '100%', height: '100%', objectFit: 'contain',
      background: 'rgba(0,0,0,0.25)',
      opacity: 0,
      transition: 'opacity 400ms ease, transform 400ms ease',
      transform: 'scale(1)'
    },
    carouselImgActive: { opacity: 1, transform: 'scale(1.01)' },
    arrowBase: {
      position: 'absolute', top: '50%', transform: 'translateY(-50%)',
      width: 36, height: 36, borderRadius: 18,
      display: 'flex', alignItems: 'center', justifyContent: 'center',
      background: 'rgba(0,0,0,0.35)', color: '#fff',
      border: '1px solid rgba(255,255,255,0.25)',
      cursor: 'pointer', userSelect: 'none'
    },
    arrowLeft: { left: 8 },
    arrowRight: { right: 8 },
    dots: {
      position: 'absolute', left: 0, right: 0, bottom: 8,
      display: 'flex', gap: 6, justifyContent: 'center', alignItems: 'center'
    },
    dot: { width: 8, height: 8, borderRadius: 4, background: 'rgba(255,255,255,0.45)', cursor: 'pointer' },
    dotActive: { background: '#fff' },
    // ---
    contentBody: { padding: 16, textAlign: 'left', color: isDark ? '#E8EDF2' : '#0B1B2B' },
    contentTitle: { margin: '0 0 6px', fontSize: 20, fontWeight: 800, color: isDark ? '#FFFFFF' : '#0B1B2B' },
    contentDescPlain: { margin: '8px 0 14px', fontSize: 14, opacity: 0.9, lineHeight: 1.5, whiteSpace: 'pre-line', color: isDark ? 'rgba(244,246,250,0.85)' : 'rgba(11,27,43,0.8)' },
    contentDescHtml: {
      margin: '8px 0 14px', fontSize: 14, opacity: 0.9, lineHeight: 1.5,
      display: 'inline-block', textAlign: 'left', maxWidth: 'min(900px, 100%)',
      color: isDark ? 'rgba(244,246,250,0.85)' : 'rgba(11,27,43,0.8)'
    },
    btnRow: { display: 'flex', alignItems: 'center', gap: 10, flexWrap: 'wrap', marginTop: 8 },
    dlBtn: {
      display: 'inline-flex', alignItems: 'center', gap: 10,
      padding: '12px 16px', borderRadius: 12, textDecoration: 'none',
      background: 'linear-gradient(180deg, #111827, #0b1324)',
      color: '#ffffff',
      width: '22%',
      justifyContent: 'center',
      alignItems: 'center',
      border: '1px solid rgba(121, 121, 121, 0.25)',
      transform: 'translateZ(0)',
      transition: 'transform 160ms ease, box-shadow 160ms ease, opacity 160ms ease',
    },
    dlIcon: {
      width: 22, height: 22, display: 'inline-block',
      filter: 'drop-shadow(0 1px 2px rgba(0,0,0,0.3))'
    },
    placeholder: { height: 220, borderRadius: 16, border: '1px solid rgba(128,128,128,0.25)', display: 'flex', alignItems: 'center', justifyContent: 'center', opacity: 0.75 },
    // modal styles
    modalOverlay: { position: 'fixed', inset: 0, zIndex: 2300, background: 'rgba(0,0,0,0.5)', display: 'flex', alignItems: 'center', justifyContent: 'center', backdropFilter: 'blur(2px)' },
    modalCard: { width: 'min(92vw, 420px)', borderRadius: 14, padding: 18, background: isDark ? '#171D24' : '#FFFFFF', color: isDark ? '#F4F6FA' : '#0B1B2B', border: isDark ? '1px solid rgba(255,255,255,0.08)' : '1px solid rgba(0,0,0,0.08)', boxShadow: isDark ? '0 18px 40px rgba(0,0,0,0.5)' : '0 18px 40px rgba(0,0,0,0.08)' },
    modalTitle: { fontSize: 18, fontWeight: 800, marginBottom: 8 },
    modalText: { fontSize: 13, opacity: 0.82, marginBottom: 12 },
    // Password modal row and fields
    modalPwdRow: { display: 'flex', alignItems: 'stretch', gap: 8, marginBottom: 14 },
    modalPwdField: { flex: 1, display: 'flex', alignItems: 'center', justifyContent: 'center',
      fontFamily: 'ui-monospace, SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace',
      fontSize: 18, fontWeight: 900, letterSpacing: 1,
      padding: '10px 12px', borderRadius: 10,
      background: isDark ? 'rgba(255,255,255,0.06)' : 'rgba(0,0,0,0.05)',
      border: isDark ? '1px solid rgba(255,255,255,0.12)' : '1px solid rgba(0,0,0,0.06)'
    },
    modalCopyBtn: { width: 44, minWidth: 44, height: 44, borderRadius: 10,
      display: 'inline-flex', alignItems: 'center', justifyContent: 'center',
      background: isDark ? 'rgba(17, 24, 39, 0.35)' : '#f5f7fb',
      border: '1px solid rgba(125,125,125,0.25)', cursor: 'pointer',
      color: isDark ? '#fff' : '#000' },
    modalRow: { display: 'flex', gap: 12, justifyContent: 'center' },
    modalBtn: { padding: '10px 14px', borderRadius: 10, border: '1px solid rgba(125,125,125,0.25)', background: isDark ? 'rgba(17, 24, 39, 0.35)' : '#f5f7fb', cursor: 'pointer', fontWeight: 700 },
    modalBtnPrimary: { background: 'linear-gradient(180deg, #111827, #0b1324)', color: '#fff', fontSize: 16, padding: '14px 22px' },
  };

  // Responsive adjustments (only image height)
  React.useEffect(() => {
    const handleResize = () => {
      const root = document.documentElement;
      if (window.innerWidth <= 768) {
        root.style.setProperty('--card-img-height', '360px');
      } else {
        root.style.setProperty('--card-img-height', '460px');
      }
    };
    handleResize();
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  const Card = ({ g }) => {
    const ref = useRef(null);
    const onEnter = () => {
      if (ref.current) Object.assign(ref.current.style, styles.cardHover);
      if (ref.current) {
        const img = ref.current.querySelector('img');
        if (img) img.style.transform = `scale(${(g.scale ?? 1) * 1.03})`;
      }
    };
    const onLeave = () => {
      if (ref.current) Object.assign(ref.current.style, { transform: 'none', boxShadow: 'none', borderColor: 'rgba(128,128,128,0.25)' });
      if (ref.current) {
        const img = ref.current.querySelector('img');
        if (img) img.style.transform = `scale(${g.scale ?? 1})`;
      }
    };
    return (
      <div ref={ref} onMouseEnter={onEnter} onMouseLeave={onLeave} onClick={() => scrollTo(g.key)} style={styles.card}>
        <div style={styles.imgWrap}>
          {g.img ? (
            <img src={g.img} alt={t.games[g.key] || g.key} style={{ ...styles.img, transform: `scale(${g.scale ?? 1})` }} loading="lazy" draggable="false" />
          ) : (
            <div style={{ fontSize: 64, opacity: 0.25 }}>{(t.games[g.key] || g.key).slice(0,1)}</div>
          )}
        </div>
        <div style={styles.title}>{t.games[g.key]}</div>
      </div>
    );
  };

  const ContentCard = ({ item, onDownloadClick }) => {
    // Build images array priority: image1/2/3 -> images[] -> img x3
    const trio = [item.image1, item.image2, item.image3].filter(Boolean);
    const images = trio.length > 0
      ? trio
      : (Array.isArray(item.images) && item.images.length > 0)
        ? item.images
        : [item.img, item.img, item.img];

    const resolved = images.map((p) => resolveAsset(p));
    const imgCount = resolved.length;

    const [idx, setIdx] = React.useState(0);
    const [hovered, setHovered] = React.useState(false);
    const containerRef = React.useRef(null);
    const [visible, setVisible] = React.useState(false);

    // IntersectionObserver: play only when at least ~50% of the card is on screen
    React.useEffect(() => {
      const el = containerRef.current;
      if (!el) return;
      const obs = new IntersectionObserver((entries) => {
        const e = entries[0];
        setVisible(e.isIntersecting && e.intersectionRatio >= 0.5);
      }, { threshold: [0, 0.25, 0.5, 0.75, 1] });
      obs.observe(el);
      return () => obs.disconnect();
    }, []);

    // Auto-advance when visible or hovered
    React.useEffect(() => {
      if (!imgCount) return;
      if (!(visible || hovered)) return;
      const id = setInterval(() => {
        setIdx((i) => (i + 1) % imgCount);
      }, 2500);
      return () => clearInterval(id);
    }, [visible, hovered, imgCount]);

    const prev = () => setIdx((i) => (i - 1 + imgCount) % imgCount);
    const next = () => setIdx((i) => (i + 1) % imgCount);

    const imgSrc = resolveAsset(images[0]); // kept for alt fallback
    const videoHref = item.video ? resolveAsset(item.video) : null;
    const rawDl = item.downloadpath || item.download || item.href;
    const dlHref = rawDl && rawDl !== '#' ? resolveAsset(rawDl) : null;

    return (
      <div style={styles.contentCard} ref={containerRef} onMouseEnter={() => setHovered(true)} onMouseLeave={() => setHovered(false)}>
        {/* Carousel */}
        <div style={styles.contentImgBox}>
          {resolved.map((src, i) => (
            <img
              key={i}
              src={src}
              alt={item.title}
              style={{
                ...styles.carouselImg,
                ...(i === idx ? styles.carouselImgActive : null),
              }}
              loading="lazy"
            />
          ))}
          {/* Arrows */}
          <div style={{ ...styles.arrowBase, ...styles.arrowLeft }} onClick={prev} aria-label="Prev">❮</div>
          <div style={{ ...styles.arrowBase, ...styles.arrowRight }} onClick={next} aria-label="Next">❯</div>
          {/* Dots */}
          <div style={styles.dots}>
            {resolved.map((_, i) => (
              <div
                key={i}
                onClick={() => setIdx(i)}
                style={{ ...styles.dot, ...(i === idx ? styles.dotActive : null) }}
              />
            ))}
          </div>
        </div>

        {/* Body */}
        <div style={styles.contentBody}>
          <h3 style={styles.contentTitle}>{item.title}</h3>
          {(() => {
            const d = tx(item.description);
            if (typeof d === 'string' && /<\/?[a-z][\s\S]*>/i.test(d)) {
              return (
                <div
                  style={styles.contentDescHtml}
                  dangerouslySetInnerHTML={{ __html: d }}
                />
              );
            }
            return <p style={styles.contentDescPlain}>{d}</p>;
          })()}
          <div style={styles.btnRow}>
            {/* Download first */}
            {dlHref && (
              <a
                href={dlHref}
                style={{ ...styles.dlBtn, marginLeft: 6, textDecoration: 'none' }}
                onClick={(e) => { e.preventDefault(); onDownloadClick(dlHref); }}
                onMouseEnter={(e) => { e.currentTarget.style.transform = 'translateY(-2px)'; e.currentTarget.style.boxShadow = '0 10px 22px rgba(0,0,0,0.22), inset 0 1px 0 rgba(255,255,255,0.06)'; }}
                onMouseLeave={(e) => { e.currentTarget.style.transform = 'translateY(0)'; e.currentTarget.style.boxShadow = '0 6px 16px rgba(0,0,0,0.18), inset 0 1px 0 rgba(255,255,255,0.05)'; }}
              >
                <svg aria-hidden="true" focusable="false" width="16" height="16" viewBox="0 0 24 22" fill="currentColor" style={styles.dlIcon}>
                  <path d="M12 3a1 1 0 0 1 1 1v8.586l2.293-2.293a1 1 0 1 1 1.414 1.414l-4.007 4.007a1.25 1.25 0 0 1-1.778 0L6.915 11.707a1 1 0 0 1 1.414-1.414L10.5 12.464V4a1 1 0 0 1 1-1z"/>
                  <path d="M5 18a1 1 0 0 1 1-1h12a1 1 0 1 1 0 2H6a1 1 0 0 1-1-1z"/>
                </svg>
                <span>{t.download}</span>
              </a>
            )}
            {/* Optional Video button on the right */}
            {/* {videoHref && (
              <a
                href={videoHref}
                target="_blank"
                rel="noreferrer"
                style={{ ...styles.dlBtn, padding: '14px 22px' }}
                onMouseEnter={(e) => { e.currentTarget.style.transform = 'translateY(-2px)'; e.currentTarget.style.boxShadow = '0 10px 22px rgba(0,0,0,0.22), inset 0 1px 0 rgba(255,255,255,0.06)'; }}
                onMouseLeave={(e) => { e.currentTarget.style.transform = 'translateY(0)'; e.currentTarget.style.boxShadow = '0 6px 16px rgba(0,0,0,0.18), inset 0 1px 0 rgba(255,255,255,0.05)'; }}
              >
                <svg aria-hidden="true" focusable="false" width="20" height="20" viewBox="0 0 24 24" fill="currentColor" style={styles.dlIcon}>
                  <path d="M10 8l6 4-6 4V8z" />
                </svg>
                <span>{lang === 'ru' ? 'Посмотреть видео' : 'Watch video'}</span>
              </a>
            )} */}
          </div>
        </div>
      </div>
    );
  };

  return (
    <div style={styles.page}>
      <div style={styles.headerRow}>
        <div style={styles.h1}>{t.page}</div>
        <div style={styles.sub}>{t.choose}</div>
      </div>

      {/* Big cards selector */}
      <div style={styles.grid}>
        {games.map((g) => (
          <Card key={g.key} g={g} />
        ))}
      </div>

      {/* Gap between selector and first section */}
      <div style={{ height: 150 }} />

      {/* Sections with content cards */}
      {games.map((g, i) => (
        <React.Fragment key={g.key}>
          <section ref={(el) => { sectionRefs.current[g.key] = el; }} style={styles.section}>
            <h2 style={styles.sectionTitle}>{t.games[g.key]}</h2>
            {Array.isArray(gameCards[g.key]) && gameCards[g.key].length > 0 ? (
              <div style={styles.contentGrid}>
                {gameCards[g.key].map((item) => (
                  <ContentCard key={item.id} item={item} onDownloadClick={onDownloadClick} />
                ))}
              </div>
            ) : (
              <div style={styles.placeholder}>
                {g.key === 'dota2'
                  ? (lang === 'ru' ? 'Скоро…' : 'Coming soon…')
                  : (lang === 'ru' ? 'Карточки появятся позже' : 'Cards coming soon')}
              </div>
            )}
          </section>
          {i < games.length - 1 && (
            <div style={styles.separatorWrap} aria-hidden="true">
              <div style={styles.separatorGlow} />
              <div style={styles.separatorLine} />
            </div>
          )}
        </React.Fragment>
      ))} 
    {/* Password modal */}
    {pwOpen && (
      <div style={styles.modalOverlay} onClick={(e) => { if (e.target === e.currentTarget) setPwOpen(false); }}>
        <div role="dialog" aria-modal="true" style={styles.modalCard}>
          <div style={styles.modalTitle}>{PW.title}</div>
          <div style={styles.modalText}>{PW.note}</div>
          <div style={styles.modalPwdRow}>
            <div style={styles.modalPwdField}>{PW.pass}</div>
            <button
              type="button"
              aria-label={PW.copy}
              title={PW.copy}
              style={styles.modalCopyBtn}
              onClick={() => { try { navigator.clipboard.writeText(PW.pass); } catch {} }}
            >
              <svg width="18" height="18" viewBox="0 0 24 24" fill="currentColor" aria-hidden="true">
                <path d="M16 1H4a2 2 0 0 0-2 2v12h2V3h12V1zm3 4H8a2 2 0 0 0-2 2v14a2 2 0 0 0 2 2h11a2 2 0 0 0 2-2V7a2 2 0 0 0-2-2zm0 16H8V7h11v14z"/>
              </svg>
            </button>
          </div>
          <div style={styles.modalRow}>
            <a
              href={pwHref || '#'}
              download
              style={{
                ...styles.modalBtn,
                ...styles.modalBtnPrimary,
                textDecoration: 'none',
                minWidth: '90%',
                textAlign: 'center',
                display: 'inline-flex',
                justifyContent: 'center',
                alignItems: 'center',
                gap: 10,
              }}
              onClick={() => setPwOpen(false)}
            >
              <svg width="18" height="18" viewBox="0 0 24 24" fill="currentColor" aria-hidden="true">
                <path d="M12 3a1 1 0 0 1 1 1v8.586l2.293-2.293a1 1 0 1 1 1.414 1.414l-4.007 4.007a1.25 1.25 0 0 1-1.778 0L6.915 11.707a1 1 0 0 1 1.414-1.414L10.5 12.464V4a1 1 0 0 1 1-1z"/>
                <path d="M5 18a1 1 0 0 1 1-1h12a1 1 0 1 1 0 2H6a1 1 0 0 1-1-1z"/>
              </svg>
              <span>{PW.cont}</span>
            </a>
          </div>
        </div>
      </div>
    )}
    </div>
  );
}
