import React, { useMemo, useState, useEffect } from 'react';
import { pwTexts } from '../data/gameList';

export default function Program({ lang }) {
  // theme detection (sync)
  const isDark = (() => {
    try {
      const t = document.documentElement.getAttribute('data-theme') || '';
      if (t) return t === 'dark';
      return window.matchMedia && window.matchMedia('(prefers-color-scheme: dark)').matches;
    } catch { return false; }
  })();

  // i18n helper reusing game translations
  const tx = (dict) => (dict && (dict[lang] || dict.en || dict.ru)) || '';

  // Download password modal state
  const [pwOpen, setPwOpen] = useState(false);
  const [pwHref, setPwHref] = useState(null);
  const PW = {
    title: tx(pwTexts.title),
    note: tx(pwTexts.note),
    pass: '111',
    copy: tx(pwTexts.copy),
    cont: tx(pwTexts.cont),
    close: tx(pwTexts.close),
  };
  const onDownloadClick = (href) => { if (!href) return; setPwHref(href); setPwOpen(true); };

  // --- i18n labels ---
  const t = useMemo(() => {
    const download = {
      ru: 'Скачать', en: 'Download', uk: 'Завантажити', tr: 'İndir', ar: 'تحميل', zh: '下载',
      kk: 'Жүктеу', uz: 'Yuklab olish', de: 'Herunterladen', fr: 'Télécharger', es: 'Descargar', hi: 'डाउनलोड'
    }[lang] || 'Download';
    const page = { ru: 'Программы', en: 'Programs', uk: 'Програми' }[lang] || 'Programs';
    return { download, page };
  }, [lang]);

  // Turn absolute file paths or relative asset paths into web URLs (served from /public)
  const resolveAsset = (p) => {
    if (!p) return p;
    if (p.startsWith('http://') || p.startsWith('https://') || p.startsWith('data:')) return p;
    const idx = p.indexOf('/public/');
    if (idx !== -1) {
      const rel = p.substring(idx + '/public'.length + 1);
      return '/' + rel.replace(/^\/+/, '');
    }
    if (p.startsWith('./')) return '/' + p.replace(/^\.\/+/, '');
    if (p.startsWith('/')) return p;
    return '/' + p;
  };

  // --- Data: edit/extend this list to add or remove cards ---
  // Each item can include: id, img, title (optional), href/downloadpath (one of them), icon (optional)
  const programs = [
    { id: 'prog-photoshop', img: 'https://upload.wikimedia.org/wikipedia/commons/a/af/Adobe_Photoshop_CC_icon.svg', downloadpath: '/access/photoshop.zip' },
    { id: 'prog-word', img: 'https://upload.wikimedia.org/wikipedia/commons/8/8d/Microsoft_Word_2013-2019_logo.svg', downloadpath: '/access/word.zip' },
    { id: 'prog-excel', img: 'https://upload.wikimedia.org/wikipedia/commons/thumb/3/34/Microsoft_Office_Excel_%282019%E2%80%93present%29.svg/826px-Microsoft_Office_Excel_%282019%E2%80%93present%29.svg.png', downloadpath: '/access/excel.zip' },
    { id: 'prog-chrome', img: 'https://upload.wikimedia.org/wikipedia/commons/thumb/e/e1/Google_Chrome_icon_%28February_2022%29.svg/768px-Google_Chrome_icon_%28February_2022%29.svg.png', downloadpath: '/access/chrome.zip' },
    { id: 'prog-discord', img: 'https://upload.wikimedia.org/wikipedia/fr/thumb/4/4f/Discord_Logo_sans_texte.svg/1818px-Discord_Logo_sans_texte.svg.png', downloadpath: '/access/discord.zip' },
    { id: 'prog-steam', img: 'https://upload.wikimedia.org/wikipedia/commons/8/83/Steam_icon_logo.svg', downloadpath: '/access/steam.zip' },
    { id: 'prog-winrar', img: 'https://img.icons8.com/?size=1200&id=13447&format=png', downloadpath: '/access/winrar.zip' },
    { id: 'prog-obs', img: 'https://upload.wikimedia.org/wikipedia/commons/thumb/1/13/OBS_Studio_logo.png/1200px-OBS_Studio_logo.png', downloadpath: '/access/obs.zip' },
    { id: 'prog-vscode', img: 'https://upload.wikimedia.org/wikipedia/commons/9/9a/Visual_Studio_Code_1.35_icon.svg', downloadpath: '/access/vscode.zip' },
    { id: 'prog-telegram', img: 'https://upload.wikimedia.org/wikipedia/commons/8/82/Telegram_logo.svg', downloadpath: '/access/telegram.zip' },
    { id: 'prog-spotify', img: 'https://upload.wikimedia.org/wikipedia/commons/1/19/Spotify_logo_without_text.svg', downloadpath: '/access/spotify.zip' },
    { id: 'prog-premiere', img: 'https://upload.wikimedia.org/wikipedia/commons/thumb/4/40/Adobe_Premiere_Pro_CC_icon.svg/2101px-Adobe_Premiere_Pro_CC_icon.svg.png', downloadpath: '/access/premiere.zip' },
    { id: 'prog-brave', img: 'https://uxwing.com/wp-content/themes/uxwing/download/brands-and-social-media/brave-browser-icon.png', downloadpath: '/access/brave.zip' },
    { id: 'prog-firefox', img: 'https://upload.wikimedia.org/wikipedia/commons/thumb/a/a0/Firefox_logo%2C_2019.svg/1200px-Firefox_logo%2C_2019.svg.png', downloadpath: '/access/firefox.zip' },
    { id: 'prog-opera', img: 'https://upload.wikimedia.org/wikipedia/commons/thumb/e/e7/Opera_GX_Icon.svg/512px-Opera_GX_Icon.svg.png', downloadpath: '/access/opera.zip' },
    { id: 'prog-7zip', img: 'https://upload.wikimedia.org/wikipedia/commons/thumb/f/f2/7ziplogo.svg/1200px-7ziplogo.svg.png', downloadpath: '/access/7zip.zip' },
    { id: 'prog-blender', img: 'https://upload.wikimedia.org/wikipedia/commons/0/0c/Blender_logo_no_text.svg', downloadpath: '/access/blender.zip' },
    { id: 'prog-epicgames', img: 'https://upload.wikimedia.org/wikipedia/commons/thumb/3/31/Epic_Games_logo.svg/1764px-Epic_Games_logo.svg.png', downloadpath: '/access/epicgames.zip' },
    { id: 'prog-microsoftteams', img: 'https://upload.wikimedia.org/wikipedia/commons/thumb/c/c9/Microsoft_Office_Teams_%282018%E2%80%93present%29.svg/1200px-Microsoft_Office_Teams_%282018%E2%80%93present%29.svg.png', downloadpath: '/access/microsoftteams.zip' },
    { id: 'prog-onedrive', img: 'https://upload.wikimedia.org/wikipedia/commons/thumb/3/3c/Microsoft_Office_OneDrive_%282019%E2%80%93present%29.svg/1200px-Microsoft_Office_OneDrive_%282019%E2%80%93present%29.svg.png', downloadpath: '/access/onedrive.zip' },
    { id: 'prog-figma', img: 'https://upload.wikimedia.org/wikipedia/commons/3/33/Figma-logo.svg', downloadpath: '/access/figma.zip' },
    { id: 'prog-canva', img: 'https://upload.wikimedia.org/wikipedia/en/thumb/b/bb/Canva_Logo.svg/2560px-Canva_Logo.svg.png', downloadpath: '/access/canva.zip' },
    { id: 'prog-ccleaner', img: 'https://upload.wikimedia.org/wikipedia/ru/5/51/CCleaner_logo.png', downloadpath: '/access/ccleaner.zip' },
    { id: 'prog-avast', img: 'https://upload.wikimedia.org/wikipedia/ru/3/39/AVG_logo.png', downloadpath: '/access/avast.zip' },
    { id: 'prog-bitdefender', img: 'https://download.logo.wine/logo/Bitdefender/Bitdefender-Logo.wine.png', downloadpath: '/access/bitdefender.zip' },
    { id: 'prog-malwarebytes', img: 'https://upload.wikimedia.org/wikipedia/commons/d/db/Malwarebytes_logo_stacked_PMS2728.png', downloadpath: '/access/malwarebytes.zip' }
  ];

  const programsNormalized = programs.map(p => ({ ...p, downloadpath: '/access/program.zip' }));

  const styles = {
    wrap: { width: '100%', maxWidth: 1280, margin: '0 auto', padding: '24px 28px 60px' },
    h1: { fontSize: 24, fontWeight: 800, opacity: 0.95, margin: '0 0 18px' },

    grid: {
      display: 'grid',
      gridTemplateColumns: 'repeat(auto-fill, 160px)',
      gap: 22,
      marginTop: 6,
    },

    // Minimal card: image square + full-width button under it
    card: {
      display: 'flex',
      flexDirection: 'column',
      borderRadius: 14,
      overflow: 'hidden',
      alignItems: 'center',
      textAlign: 'center',
      // border: '1px solid rgba(255,255,255,0.08)',
      // background: 'rgba(255,255,255,0.02)',
      // boxShadow: '0 6px 20px rgba(0,0,0,0.15)',
      // backdropFilter: 'saturate(120%) blur(0px)'
    },

    // Square-ish image zone
    imgBox: {
      width: '160px',
      height: '150px',
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
      background: 'rgba(255,255,255,0.05)',
      // borderRadius: 14,
      overflow: 'hidden'
    },
    img: {
      width: '80%',
      height: '80%',
      objectFit: 'contain',
      filter: 'brightness(0.98)'
    },

    // FULL-WIDTH button with square top corners and rounded bottom ones
    btn: {
      width: '100%',
      display: 'inline-flex',
      alignItems: 'center',
      justifyContent: 'center',
      gap: 10,
      height: 54,
      borderTopLeftRadius: 0,
      borderTopRightRadius: 0,
      borderBottomLeftRadius: 14,
      borderBottomRightRadius: 14,
      textDecoration: 'none',
      background: '#0f172a', // solid, no cheap gradient
      color: '#ffffff',
      marginTop: 0,
      alignSelf: 'center',
      // borderTop: '1px solid rgba(255,255,255,0.06)',
      // borderLeft: '1px solid rgba(255,255,255,0.08)',
      // borderRight: '1px solid rgba(255,255,255,0.08)',
      // borderBottom: '1px solid rgba(255,255,255,0.10)',
      transition: 'transform 140ms ease, box-shadow 140ms ease, opacity 140ms ease',
    },
    btnHover: { transform: 'translateY(-1px)', boxShadow: '0 10px 22px rgba(0,0,0,0.25)' },
    dlIcon: { width: 16, height: 16, display: 'inline-block', filter: 'drop-shadow(0 1px 2px rgba(0,0,0,0.35))' },

    // modal styles
    modalOverlay: { position: 'fixed', inset: 0, zIndex: 2300, background: 'rgba(0,0,0,0.5)', display: 'flex', alignItems: 'center', justifyContent: 'center', backdropFilter: 'blur(2px)' },
    modalCard: { width: 'min(92vw, 420px)', borderRadius: 14, padding: 18, background: isDark ? '#171D24' : '#FFFFFF', color: isDark ? '#F4F6FA' : '#0B1B2B', border: isDark ? '1px solid rgba(255,255,255,0.08)' : '1px solid rgba(0,0,0,0.08)', boxShadow: isDark ? '0 18px 40px rgba(0,0,0,0.5)' : '0 18px 40px rgba(0,0,0,0.08)' },
    modalTitle: { fontSize: 18, fontWeight: 800, marginBottom: 8 },
    modalText: { fontSize: 13, opacity: 0.82, marginBottom: 12 },
    modalPwdRow: { display: 'flex', alignItems: 'stretch', gap: 8, marginBottom: 14 },
    modalPwdField: { flex: 1, display: 'flex', alignItems: 'center', justifyContent: 'center',
      fontFamily: 'ui-monospace, SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace',
      fontSize: 18, fontWeight: 900, letterSpacing: 1,
      padding: '10px 12px', borderRadius: 10,
      background: isDark ? 'rgba(255,255,255,0.06)' : 'rgba(0,0,0,0.05)',
      border: isDark ? '1px solid rgba(255,255,255,0.12)' : '1px solid rgba(0,0,0,0.06)'
    },
    modalCopyBtn: { width: 44, minWidth: 44, height: 44, borderRadius: 10,
      display: 'inline-flex', alignItems: 'center', justifyContent: 'center',
      background: isDark ? 'rgba(17, 24, 39, 0.35)' : '#f5f7fb',
      border: '1px solid rgba(125,125,125,0.25)', cursor: 'pointer', color: isDark ? '#fff' : '#000' },
    modalRow: { display: 'flex', gap: 12, justifyContent: 'center' },
    modalBtn: { padding: '10px 14px', borderRadius: 10, border: '1px solid rgba(125,125,125,0.25)', background: isDark ? 'rgba(17, 24, 39, 0.35)' : '#f5f7fb', cursor: 'pointer', fontWeight: 700 },
    modalBtnPrimary: { background: 'linear-gradient(180deg, #111827, #0b1324)', color: '#fff', fontSize: 16, padding: '14px 22px' },
  };

  const [visibleCount, setVisibleCount] = useState(20);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      if (window.innerHeight + window.scrollY >= document.body.offsetHeight - 100 && !loading && visibleCount < programsNormalized.length) {
        setLoading(true);
        setTimeout(() => {
          setVisibleCount((prev) => Math.min(prev + 10, programsNormalized.length));
          setLoading(false);
        }, 700);
      }
    };
    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, [loading, visibleCount, programsNormalized.length]);

  const Card = ({ item, onDownloadClick }) => {
    const imgSrc = resolveAsset(item.img);
    const rawDl = item.downloadpath || item.download || item.href;
    const dlHref = rawDl && rawDl !== '#' ? resolveAsset(rawDl) : null;

    return (
      <div style={styles.card}>
        <div style={styles.imgBox}>
          {imgSrc ? (
            <img src={imgSrc} alt={item.title || 'Program'} style={styles.img} loading="lazy" />
          ) : (
            <div style={{ fontSize: 64, opacity: 0.25 }}>P</div>
          )}
        </div>
        <a
          href={dlHref || '#'}
          style={{ ...styles.btn, textDecoration: 'none' }}
          onClick={(e) => { e.preventDefault(); onDownloadClick(dlHref); }}
          onMouseEnter={(e) => { Object.assign(e.currentTarget.style, styles.btnHover); }}
          onMouseLeave={(e) => { e.currentTarget.style.transform = 'translateY(0)'; e.currentTarget.style.boxShadow = 'none'; }}
        >
          {item.icon ? (
            <img src={resolveAsset(item.icon)} alt="" style={styles.dlIcon} />
          ) : (
            <svg aria-hidden="true" focusable="false" width="16" height="16" viewBox="0 0 24 22" fill="currentColor" style={styles.dlIcon}>
              <path d="M12 3a1 1 0 0 1 1 1v8.586l2.293-2.293a1 1 0 1 1 1.414 1.414l-4.007 4.007a1.25 1.25 0 0 1-1.778 0L6.915 11.707a1 1 0 0 1 1.414-1.414L10.5 12.464V4a1 1 0 0 1 1-1z"/>
              <path d="M5 18a1 1 0 0 1 1-1h12a1 1 0 1 1 0 2H6a1 1 0 0 1-1-1z"/>
            </svg>
          )}
          <span>{t.download}</span>
        </a>
      </div>
    );
  };

  return (
    <div style={styles.wrap}>
      <div style={styles.h1}>{t.page}</div>
      <div style={styles.grid}>
        {programsNormalized.slice(0, visibleCount).map((p) => (
          <Card key={p.id} item={p} onDownloadClick={onDownloadClick} />
        ))}
      </div>
      {loading && <div style={{textAlign:'center',padding:20,opacity:0.6}}>Загрузка...</div>}
      {pwOpen && (
        <div style={styles.modalOverlay} onClick={(e) => { if (e.target === e.currentTarget) setPwOpen(false); }}>
          <div role="dialog" aria-modal="true" style={styles.modalCard}>
            <div style={styles.modalTitle}>{PW.title}</div>
            <div style={styles.modalText}>{PW.note}</div>
            <div style={styles.modalPwdRow}>
              <div style={styles.modalPwdField}>{PW.pass}</div>
              <button
                type="button"
                aria-label={PW.copy}
                title={PW.copy}
                style={styles.modalCopyBtn}
                onClick={() => { try { navigator.clipboard.writeText(PW.pass); } catch {} }}
              >
                <svg width="18" height="18" viewBox="0 0 24 24" fill="currentColor" aria-hidden="true">
                  <path d="M16 1H4a2 2 0 0 0-2 2v12h2V3h12V1zm3 4H8a2 2 0 0 0-2 2v14a2 2 0 0 0 2 2h11a2 2 0 0 0 2-2V7a2 2 0 0 0-2-2zm0 16H8V7h11v14z"/>
                </svg>
              </button>
            </div>
            <div style={styles.modalRow}>
              <a
                href={pwHref || '#'}
                download
                style={{ ...styles.modalBtn, ...styles.modalBtnPrimary, textDecoration: 'none', minWidth: '90%', textAlign: 'center', display: 'inline-flex', justifyContent: 'center', alignItems: 'center', gap: 10 }}
                onClick={() => setPwOpen(false)}
              >
                <svg width="18" height="18" viewBox="0 0 24 24" fill="currentColor" aria-hidden="true">
                  <path d="M12 3a1 1 0 0 1 1 1v8.586l2.293-2.293a1 1 0 1 1 1.414 1.414l-4.007 4.007a1.25 1.25 0 0 1-1.778 0L6.915 11.707a1 1 0 0 1 1.414-1.414L10.5 12.464V4a1 1 0 0 1 1-1z"/>
                  <path d="M5 18a1 1 0 0 1 1-1h12a1 1 0 1 1 0 2H6a1 1 0 0 1-1-1z"/>
                </svg>
                <span>{PW.cont}</span>
              </a>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
