export default function Contact({ appStyles, lang }) {
  const t = (tMap && (tMap[lang] || tMap.ru || tMap.en)) || {};

  // --- THEME SYNC (как на главной) ---
  const getInitialTheme = () => {
    if (typeof window !== 'undefined') {
      const saved = localStorage.getItem('theme');
      if (saved === 'light' || saved === 'dark') return saved;
      if (window.matchMedia && window.matchMedia('(prefers-color-scheme: dark)').matches) return 'dark';
    }
    return 'light';
  };
  const [theme, setTheme] = useState(getInitialTheme);
  useEffect(() => {
    const mq = window.matchMedia ? window.matchMedia('(prefers-color-scheme: dark)') : null;
    const onMq = (e) => {
      const saved = localStorage.getItem('theme');
      if (!saved) setTheme(e.matches ? 'dark' : 'light');
    };
    const onApp = (e) => {
      const next = e.detail;
      if (next === 'light' || next === 'dark') setTheme(next);
    };
    mq && (mq.addEventListener ? mq.addEventListener('change', onMq) : mq.addListener(onMq));
    window.addEventListener('app-theme-changed', onApp);
    const saved = localStorage.getItem('theme');
    if (saved === 'light' || saved === 'dark') setTheme(saved);
    return () => {
      mq && (mq.removeEventListener ? mq.removeEventListener('change', onMq) : mq.removeListener(onMq));
      window.removeEventListener('app-theme-changed', onApp);
    };
  }, []);
  const colors = useMemo(() => {
    const common = {
      accent: '#229ED9',
      accentSoft: 'rgba(34,158,217,0.15)',
      focus: 'rgba(34,158,217,0.35)',
      radiusLg: 18,
    };
    if (theme === 'dark') {
      return {
        ...common,
        // bg: '#0F141A',
        bgSoft: '#171D24',
        text: '#F4F6FA',
        textDim: 'rgba(244,246,250,0.75)',
        cardBorder: 'rgba(255,255,255,0.08)',
        shadow: '0 10px 30px rgba(0,0,0,0.35)',
        divider: 'rgba(255,255,255,0.08)',
      };
    }
    return {
      ...common,
      bg: '#F7F9FC',
      bgSoft: '#FFFFFF',
      text: '#0B1B2B',
      textDim: 'rgba(11,27,43,0.72)',
      cardBorder: 'rgba(11,27,43,0.08)',
      shadow: '0 10px 24px rgba(34,158,217,0.12)',
      divider: 'rgba(11,27,43,0.08)',
    };
  }, [theme]);

  const [openIndex, setOpenIndex] = useState(null);
  

  const styles = {
    wrap: {
      animation: 'fadeIn 1s ease',
      width: '100%',
      maxWidth: 1280,
      margin: '0 auto',
      padding: '48px 24px 80px',
      background: colors.bg,
      color: colors.text,
      transition: 'background 200ms ease, color 200ms ease',
    },
    heroWrap: { display: 'flex', justifyContent: 'flex-start', alignItems: 'center', marginBottom: 12, width: '100%' },
    hero: { width: 180, height: 180 },
    title: { fontSize: 36, marginBottom: 10, fontWeight: 700, color: colors.text },
    tagline: { fontSize: 20, opacity: 0.85, marginBottom: 30, color: colors.text },
    text: { maxWidth: 900, textAlign: 'left', lineHeight: 1.7, marginBottom: 36, fontSize: 18, color: colors.textDim },
    buttons: { display: 'flex', gap: 20, marginBottom: 20 },
    quote: {
      marginTop: '-20px',
      marginBottom: '20px',
      padding: '12px 18px',
      borderLeft: `3px solid ${colors.accent}`,
      background: theme === 'dark' ? 'rgba(255,255,255,0.04)' : 'rgba(34,158,217,0.06)',
      borderRadius: 12,
      color: colors.text,
      lineHeight: 1.55,
      fontSize: 15,
    },
    quoteSmall: { color: colors.textDim, fontSize: 14, marginTop: 2 },
    btn: {
      display: 'inline-flex',
      alignItems: 'center',
      justifyContent: 'center',
      gap: 10,
      background: theme === 'dark' ? 'rgba(255,255,255,0.04)' : 'rgba(34,158,217,0.06)',
      color: colors.text,
      border: `1px solid ${colors.cardBorder}`,
      borderRadius: 18,
      padding: '12px 22px',
      fontSize: 16,
      cursor: 'pointer',
      textDecoration: 'none',
      transition: 'transform 0.2s ease, box-shadow 0.2s ease, background 0.2s ease, border-color 0.2s ease',
      boxShadow: colors.shadow,
      backdropFilter: 'blur(6px) saturate(130%)',
    },
    btnHover: {
      transform: 'translateY(-1px)',
      boxShadow: '0 10px 20px rgba(34,158,217,0.22)',
      background: 'linear-gradient(180deg, rgba(34,158,217,0.22), rgba(34,158,217,0.16))',
    },
    icon: { width: 20, height: 20 }, // unused for button icons below
    section: { marginTop: -54, maxWidth: 1100, textAlign: 'center', width: '100%', marginLeft: 'auto', marginRight: 'auto' },
    subtitle: { fontSize: 28, marginBottom: 20, fontWeight: 700 },
    qa: { marginBottom: 14, lineHeight: 1.65, padding: '8px 0' },
    partnersList: { display: 'flex', justifyContent: 'center', gap: 20, flexWrap: 'wrap', marginTop: 10 },
    partner: { background: 'rgba(255,255,255,0.05)', padding: '10px 18px', borderRadius: 10, fontSize: 15, opacity: 0.9, transition: 'all 0.2s ease', display: 'flex', alignItems: 'center' },
    partnerHover: { transform: 'translateY(-2px)', boxShadow: '0 6px 12px rgba(255,255,255,0.1)' },
    qRow: { display: 'flex', justifyContent: 'space-between', alignItems: 'center', cursor: 'pointer', fontWeight: 600, fontSize: 20, padding: '14px 4px' },
    partnerIcon: { width: 28, height: 28, marginRight: 10, filter: 'brightness(0.9)' },
    row: { display: 'flex', gap: 40, alignItems: 'flex-start', justifyContent: 'space-between', flexWrap: 'wrap' },
    leftCol: { flex: '1 1 560px', minWidth: 320, maxWidth: 640 },
    rightCol: { flex: '1 1 420px', minWidth: 320, maxWidth: 560 },
    faqCard: {
      background: 'transparent',
      border: 'none',
      boxShadow: 'none',
      borderRadius: 0,
      padding: 0,
    },
    dividerWrap: { display: 'flex', alignItems: 'center', gap: 14, margin: '74px 0 26px', justifyContent: 'center' },
    dividerLine: { flex: 1, height: 1, background: colors.divider, maxWidth: 530 },
    dividerLabel: {
      padding: '6px 12px',
      borderRadius: 999,
      border: `1px solid ${colors.cardBorder}`,
      background: colors.bgSoft,
      boxShadow: colors.shadow,
      fontSize: 13,
      letterSpacing: 0.3,
      color: colors.textDim,
    },
  };

  return (
    <div style={{ background: colors.bg, color: colors.text, transition: 'background 200ms ease, color 200ms ease' }}>
      <div>
        <div style={styles.wrap}>
          <div style={styles.row}>
            <div style={styles.leftCol}>
              <div style={styles.heroWrap}>
                <lottie-player
                  autoplay
                  loop
                  mode="normal"
                  src="/access/DUCKWALLET.json"
                  style={styles.hero}
                ></lottie-player>
              </div>
              <h1 style={styles.title}>{t.title}</h1>
              <div style={styles.tagline}>{t.tagline}</div>
              <p style={styles.text}>
                {String(t.text).split('\n').map((line, i, arr) => (
                  <React.Fragment key={i}>
                    {line}
                    {i < arr.length - 1 && <br />}
                  </React.Fragment>
                ))}
              </p>
              <div style={styles.quote}>
                <div><strong>Email:</strong> <a href={`mailto:${t.email || 'http://logovobezdelnikov.com/'}`} style={{ color: colors.text }}>{t.email || 'http://logovobezdelnikov.com/'}</a></div>
                <div style={styles.quoteSmall}>{t.quoteText || 'Если удобнее — пишите на почту, отвечаем так же быстро.'}</div>
              </div>
              <div style={styles.buttons}>
                <a href="https://t.me/bezdelniki4" target="_blank" rel="noopener noreferrer" style={styles.btn}
                  onMouseEnter={(e)=>Object.assign(e.currentTarget.style, styles.btnHover)}
                  onMouseLeave={(e)=>{e.currentTarget.style.transform='';e.currentTarget.style.boxShadow='';}}>
                  <svg width="22" height="22" viewBox="0 0 24 24" fill="none" aria-hidden="true" style={{ marginRight: 6 }}>
                    <path d="M4 5.5A3.5 3.5 0 0 1 7.5 2h9A3.5 3.5 0 0 1 20 5.5v6A3.5 3.5 0 0 1 16.5 15H9.2l-3.2 3.2c-.67.67-1.73.2-1.73-.75V5.5Z" stroke="currentColor" strokeWidth="1.8" fill="none" strokeLinecap="round" strokeLinejoin="round"/>
                    <circle cx="9" cy="9" r="1" fill="currentColor"/>
                    <circle cx="12" cy="9" r="1" fill="currentColor"/>
                    <circle cx="15" cy="9" r="1" fill="currentColor"/>
                  </svg>
                  {t.write}
                </a>
                <a href="https://t.me/bezdelniki4" target="_blank" rel="noopener noreferrer" style={styles.btn}
                  onMouseEnter={(e)=>Object.assign(e.currentTarget.style, styles.btnHover)}
                  onMouseLeave={(e)=>{e.currentTarget.style.transform='';e.currentTarget.style.boxShadow='';}}>
                  <svg width="22" height="22" viewBox="0 0 24 24" fill="none" aria-hidden="true" style={{ marginRight: 6 }}>
                    <path d="M22 3.75L18.85 20.04c-.24 1.21-1 1.5-2.02.94l-5.6-4.13-2.7 2.6c-.3.29-.56.54-1.14.54l.41-5.88 10.72-9.69c.47-.41-.1-.64-.73-.23L6.9 12.4 1.2 10.6c-1.18-.37-1.2-1.18.25-1.75L20.48 2.06c.9-.33 1.7.21 1.52 1.69Z" fill="currentColor"/>
                  </svg>
                  {t.channel}
                </a>
              </div>
            </div>
            <div style={styles.rightCol}>
              <div style={styles.faqCard}>
                <h2 style={{ ...styles.subtitle, marginBottom: 10, color: colors.text }}>{t.faqTitle}</h2>
                {t.faq.map((q, i) => (
                  <div key={i} style={styles.qa}>
                    <div
                      onClick={() => setOpenIndex(openIndex === i ? null : i)}
                      style={styles.qRow}
                    >
                      <span>{q.q}</span>
                      <svg width="20" height="20" viewBox="0 0 24 24" style={{ transform: openIndex === i ? 'rotate(90deg)' : 'rotate(0)', transition: 'transform 0.25s ease' }} aria-hidden="true">
                        <path d="M9 6l6 6-6 6" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
                      </svg>
                    </div>
                    <div style={{
                      maxHeight: openIndex === i ? '150px' : '0',
                      opacity: openIndex === i ? 1 : 0,
                      overflow: 'hidden',
                      transition: 'all 0.3s ease',
                      marginTop: openIndex === i ? 8 : 0,
                    }}>
                      <p style={{ margin: 0, color: colors.textDim }}>{q.a}</p>
                    </div>
                  </div>
                ))}
              </div>
            </div>
          </div>

          <div style={styles.dividerWrap}>
            <div style={styles.dividerLine} />
            <div style={styles.dividerLabel}>{t.collabTitle || 'С кем мы сотрудничаем'}</div>
            <div style={styles.dividerLine} />
          </div>

        </div>  

        {/* Partners */}
        <div style={styles.section} className="fadeInUp">
          <div style={styles.partnersList}>
            {[
              '/access/unicore-preview.png',
              '/access/midnight-preview.png',
              '/access/solara-preview.png',
              '/access/memez-preview.webp',
            ].map((icon, i) => (
              <img
                key={i}
                src={icon}
                alt={`partner-${i}`}
                style={{ width: 150, height: 150, objectFit: 'contain', borderRadius: 18, transition: 'transform 0.25s ease, filter 0.25s ease', filter: 'brightness(0.96)' }}
                onMouseEnter={(e)=>{ e.currentTarget.style.transform='scale(1.05)'; e.currentTarget.style.filter='brightness(1)'; }}
                onMouseLeave={(e)=>{ e.currentTarget.style.transform=''; e.currentTarget.style.filter='brightness(0.96)'; }}
              />
            ))}
          </div>
        </div>
      </div>
    </div>
  );
}

